body {
  background: #ECEFF1;
}

.card {
  background: #fff;
  border-radius: 0;
  border: none;
  height: 140px;
  margin: 1rem;
  position: relative;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}

.card > .img-container > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}



